import axios from 'axios';
import socket from '../../socket';
import {
  gotConversations,
  addConversation,
  setNewMessage,
  setSearchedUsers,
  updateConversation
} from '../conversations';
import { gotUser, setFetchingStatus } from '../user';

// USER THUNK CREATORS

export const fetchUser = () => async (dispatch) => {
  dispatch(setFetchingStatus(true));
  try {
    const { data } = await axios.get('/auth/user');
    dispatch(gotUser(data));
    if (data.id) {
      socket.emit('go-online', data.id);
    }
  } catch (error) {
    console.error(error);
  } finally {
    dispatch(setFetchingStatus(false));
  }
};

export const register = (credentials) => async (dispatch) => {
  try {
    const { data } = await axios.post('/auth/register', credentials);
    dispatch(gotUser(data));
    socket.emit('go-online', data.id);
  } catch (error) {
    console.error(error);
    dispatch(gotUser({ error: error.response.data.error || 'Server Error' }));
  }
};

export const login = (credentials) => async (dispatch) => {
  try {
    const { data } = await axios.post('/auth/login', credentials);
    dispatch(gotUser(data));
    socket.emit('go-online', data.id);
  } catch (error) {
    console.error(error);
    dispatch(gotUser({ error: error.response.data.error || 'Server Error' }));
  }
};

export const logout = (id) => async (dispatch) => {
  try {
    await axios.delete('/auth/logout');
    dispatch(gotUser({}));
    socket.emit('logout', id);
  } catch (error) {
    console.error(error);
  }
};

export const updateUnreadMessages =
  (messages, userId, otherUserId) => async (dispatch) => {
    // are there un read messages & are the messages sent by the otherUser?
    const unreadMessageIds = [];

    let idx = messages.length - 1;

    while (idx >= 0) {
      if (messages[idx].senderId !== userId && !messages[idx].readStatus) {
        unreadMessageIds.push(messages[idx].id);
        messages[idx].readStatus = true;
      }

      if (messages[idx].senderId !== userId && messages[idx].readStatus) {
        break;
      }

      idx--;
    }

    const payload = {
      unreadMessageIds
    };
    // send api call to update the messages
    if (unreadMessageIds.length > 0) {
      try {
        await axios.patch('/api/messages', {
          data: payload
        });
        dispatch(updateConversation(messages));
        socket.emit('read-messages', {
          messages,
          to: otherUserId
        });
      } catch (err) {
        console.log(err);
      }
    }
  };

// CONVERSATIONS THUNK CREATORS

export const fetchConversations = () => async (dispatch) => {
  try {
    const { data } = await axios.get('/api/conversations');
    dispatch(gotConversations(data));
  } catch (error) {
    console.error(error);
  }
};

const saveMessage = async (body) => {
  const { data } = await axios.post('/api/messages', body);
  return data;
};

const sendMessage = (data, body) => {
  socket.emit('new-message', {
    data,
    to: body.recipientId
  });
};

// message format to send: {recipientId, text, conversationId}
// conversationId will be set to null if its a brand new conversation
export const postMessage = (body) => async (dispatch) => {
  try {
    const data = await saveMessage(body);

    if (!body.conversationId) {
      dispatch(addConversation(body.recipientId, data.message));
    } else {
      dispatch(setNewMessage(data.message));
    }

    sendMessage(data, body);
  } catch (error) {
    console.error(error);
  }
};

export const searchUsers = (searchTerm) => async (dispatch) => {
  try {
    const { data } = await axios.get(`/api/users/${searchTerm}`);
    dispatch(setSearchedUsers(data));
  } catch (error) {
    console.error(error);
  }
};
